














































import common from '../../common/common.js';
import uniLoadMore from '../../components/uni-load-more.vue'

import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			pushlist: [],
			lifecat: "",
			totallist: [],
			loadingType: 0,
			contentText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			},
			page: 1,
		}
	},
	computed: {
		...mapState(['user']),

	},

	methods: {
		islogin: function() {
			if (this.user.user === null) {
				uni.reLaunch({
					url: '../../pages/manage/login'
				})
				return;
			}

		},
		message:function(pushid){
			uni.navigateTo({
				url: "trainmessage?pushid=" + pushid
			})
		},
		getpush: function(page) {

			let that = this;
			let list = [];
			//console.log(this.user.user.token);
			uni.request({
				url: common.hosturl + "index/getpush.html",
				method: "POST",
				data: {
					token: this.user.user.token,
					lifecat: this.lifecat,
					page: page

				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: function(res) {
					//console.log(JSON.stringify(res.data));
					if (res.data.code == 0) {
						that.pushlist = res.data.list;
						res.data.list.forEach(function(value, key) {

							//console.log(JSON.stringify(value));
							that.totallist.push(value);
						})



					} else {
						uni.showToast({
							icon: 'none',
							title: '出错啦，请联系客服'
						});
					}
				},
				fail: function() {
					uni.showToast({
						icon: 'none',
						title: '网络错误'
					});
				}
			})



		},
		add: function(lifecat, pushid) {
			console.log(pushid);
			let that = this;
			uni.request({
				url: common.hosturl + "index/checkpush.html",
				method: "POST",
				data: {
					token: this.user.user.token,
					pushid: pushid
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: function(res) {
					console.log(JSON.stringify(res.data));
					if (res.data.code == 0) {


						let param = {

							lifecat: lifecat,
							pushid: pushid
						}


						if (res.data.result == 1) {
							uni.navigateTo({
								url: "add?param=" + JSON.stringify(param)
							})
							return;
						} else if (res.data.result == 2) {
							uni.showToast({
								icon: 'none',
								title: '任务已过期'
							});
							return;
						} else if (res.data.result == 3) {

							uni.showToast({
								icon: 'none',
								title: '您所属的中队已经完成该任务啦'
							});
							return;
						} else if(res.data.result==4){
							return ;
						}
						else {
							uni.showToast({
								icon: 'none',
								title: '出错啦，请联系客服'
							});
							return;
						}

					} else {
						uni.showToast({
							icon: 'none',
							title: '出错啦，请联系客服'
						});
					}
				},
				fail: function() {
					uni.showToast({
						icon: 'none',
						title: '网络错误'
					});
				}
			})

		},



	},
	onReachBottom() {

		console.log(this.pushlist);
		if (this.pushlist.length < 10) {
			this.loadingType = 2
			return;
		}



		this.loadingType = 1;
		this.page++;
		this.getpush(this.page);
		if (this.loadingType !== 0) {
			return;
		}


	},
	components: {
		uniLoadMore
	},
	onLoad: function(e) {
		this.lifecat = e.lifecat
		this.islogin();
		this.getpush(this.page);




	},
	onShow: function() {





	},

	onUnload() {

		this.totallist = [];
		this.pushlist = [];
	}

}



